@import url(https://fonts.googleapis.com/css2?family=Lora:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  background-color: #fff;
  color: #555;
  font-family: 'Lora', serif;
  font-weight: 400;
  font-size: 20px;
  text-rendering: optimizeLegibility;
  overflow-x: hidden;
  overflow-y: hidden;
}


@media only screen and (max-width: 1200px){
  .App {
      font-size: 18px;
  }
}



.header {
    position: relative;
    background-color:#fff1d9;
    height: 100vh;
    width: 100vw;
    
}

.img-frame {
    position: absolute;
    min-width: 65vw;
    min-height: 100%;
    left: 5vw;
    z-index: 1;
}

.img {
    position: absolute;
    display: inline;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url(data:image/png;base64,);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
}

@media only screen and (max-width: 480px){
    .img{
        background-image: url(data:image/png;base64,);
    }
}
.side-bar {
    position: absolute;
    height: 100vh;
    width: 5vw;
    background-color: #467999;
}



.title {
    position: absolute;
    bottom: 5%;
    font-weight: 500;
    letter-spacing: 7px;
    color: #1d1b1b;
    text-transform: uppercase;
    transform-origin: 0 0;
    left: 50%;
    transform: rotate(-90deg) translateY(-50%);
    font-size: 250%;
}

@media only screen and (max-width: 1366px){
    .title {
        font-size: 200%;
    }
}
.btn-ham-back {
    position: relative;
    width: 44px;
    height: 28px;
    top: 7%;
    left: 50%;
    right: auto;
    transform: translate(-50%, -50%);
    background-color: transparent;
    z-index: 1000;
}

.btn-ham-back-mobile {
    position: relative;
    width: 44px;
    height: 28px;
    top: 50%;
    left: 50%;
    right: auto;
    transform: translate(-50%, -50%);
    background-color: transparent;
    z-index: 1000;  
}

.btn-ham-back:hover {
    cursor: pointer;
}
.btn-hamburger {
    position: absolute;
    background-color: #1d1b1b;
    height: 3px;
    width: 16px;
    top: 50%;
    z-index: 1;
}

.btn-hamburger, .btn-hamburger-before, .btn-hamburger-after {
    
    transition: all .3s;
}

.btn-hamburger-before {
    position: absolute;
    background-color: #1d1b1b;
    height: 3px;
    width: 24px;
    top: 1.5px;
    z-index: 1;
    
}

.btn-hamburger-after{
    position: absolute;
    background-color: #1d1b1b;
    height: 3px;
    width: 34px;
    bottom: 0;
    z-index: 1;
}
.mobile-nav-bar {
    position: absolute;
    width: 100%;
    height: 10%;
    background-color: #467999;
}

.languages {
    position: absolute;
    right: 10%;
    top: 10%;
    color: #555;
}

.languages-li {
    display: inline-block;
    margin: 5px;
}
.languages-links:link, languages-links:visited {
    color: #555;
    text-decoration: none;
}

@media only screen and (max-width: 800px){
    .languages {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        display: none;
    }
}
.menu-box {
    position: absolute;
    top: 0;
    left: 5vw;
    width: 65vw;
    height: 100vh;
    background-color: #467999;
    display: none;
    animation: moveFromUp .8s forwards;
    animation-iteration-count: 1;
    z-index: 100;
}


@keyframes moveFromUp {
    0% {
        height: 0;
    }
    100% {
        height: 100%;
        visibility: visible;
      } 
  }

  @keyframes moveToUp {
    0% {
        height: 100%; 
    }
    100% {
        height: 0;
        visibility: hidden;
      } 
  }

  @media only screen and (max-width: 800px){
    .menu-box {
        position: absolute;
        top: 10%;
        left: 0;
        width: 100%;
        height: 100vh;
        background-color: #467999;
        display: none;
        animation: moveFromUp .8s forwards;
        animation-iteration-count: 1;
        z-index: 100;
    }
  }
.menu-content {
    position: absolute;
    background-color: transparent;
    height: 50%;
    width: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    list-style-type: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.menu-content > a:link, a:visited {
    color: #555;
    text-decoration: none;
}

.menu-content-link {
    color: #555;
    text-decoration: none;
}



.name {
    font-weight: 500;
    color: #FFF;
    text-transform: uppercase;
    font-size: 48px;
}

.menu-sections {
    color: #1d1b1b;
    font-weight: 500;
    animation-iteration-count: 1;
    font-size: 48px;
}

@keyframes textUp {
    0% {
        font-size: 0;
    }
    100% {
        font-size: 48px;
      } 
  }

  @keyframes textPu {
    0% {
        font-size: 48px;
    }
    100% {
        font-size: 0px;
      } 
  }

  @keyframes fadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
        display: none;
      } 
  }

  @keyframes liftUp {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
        display: block;
      } 
  }



  @media only screen and (max-width: 1366px){
    .name {
        font-size: 42px;
    }

    .menu-sections {
        font-size: 36px;
    }

    @keyframes textUp {
        0% {
            font-size: 0;
        }
        100% {
            font-size: 36px;
          } 
      }
    
      @keyframes textPu {
        0% {
            font-size: 36px;
        }
        100% {
            font-size: 0px;
          } 
      }
  }

  @media only screen and (max-width: 800px){
    .name {
        font-size: 42px;
    }
    
    .menu-sections {
        width: -webkit-max-content;
        width: -moz-max-content;
        width: max-content;
        font-size: 32px;
    }

    @keyframes textUp {
        0% {
            font-size: 0;
        }
        100% {
            font-size: 32px;
          } 
      }
    
      @keyframes textPu {
        0% {
            font-size: 32px;
        }
        100% {
            font-size: 0px;
          } 
      }
  }

